<template>
  <div class="form-outline">
    <label class="label">
      <input
        type="file"
        id="file"
        ref="file"
        accept=".csv"
        v-on:change="handleFileUpload()"
      />
    </label>
    <mdb-btn class="button" color="primary" v-on:click="submitFile()"
      >Submit</mdb-btn
    >
  </div>
</template>

<script>
import { mdbBtn } from "mdbvue";
import json from "../json/sample.json";

export default {
  components: {
    mdbBtn
  },
  sample: json,
  data() {
    return {
      file: new Blob(),
      devices: []
    }
  },
  methods: {
    handleFileUpload() {
      this.file = this.$refs.file.files[0];
    },
    async submitFile() {
      await this.submitFileHelper();

      this.$emit('upload', this.devices);
    },
    submitFileHelper() {
      return new Promise((resolve, reject) => {
        const reader = new FileReader();
        
        // If no file supplied, use sample JSON
        if(this.file.size == 0) {
          reader.onload = () => {
            for(var i = 0; i < json.length; i++) {
              var tmp = json[i];

              var obj = {
                id: i,
                mac: tmp.MAC,
                ssid: tmp.SSID,
                authMode: tmp.AuthMode,
                firstSeen: tmp.FirstSeen,
                channel: tmp.Channel,
                rssi: tmp.RSSI,
                currentLatitude: tmp.CurrentLatitude,
                currentLongitude: tmp.CurrentLongitude,
                altitudeMeters: tmp.AltitudeMeters,
                accuracyMeters: tmp.AccuracyMeters,
                type: tmp.Type,
              };

              this.devices.push(obj);
            }
            resolve(this.devices);
          }
        } 
        // If file supplied, use uploaded CSV
        else {
          reader.onload = (e) => {
            var arr = e.target.result.split("\n");

            for (var i = 2; i < arr.length - 1; i++) {
              var tmp = arr[i].split(",");

              var obj = {
                id: i - 2,
                mac: tmp[0],
                ssid: tmp[1],
                authMode: tmp[2],
                firstSeen: tmp[3],
                channel: tmp[4],
                rssi: tmp[5],
                currentLatitude: tmp[6],
                currentLongitude: tmp[7],
                altitudeMeters: tmp[8],
                accuracyMeters: tmp[9],
                type: tmp[10],
              };

              this.devices.push(obj);
            }
            resolve(this.devices);
          };
        }

        // Make sure to handle error states
        reader.onerror = function(e) {
          reject(e);
        };

        reader.readAsText(this.file);
      })
    }
  }
}
</script>

