      </div>

  </div>
  <div v-else class="else">
    <h1> Upload scanned devices to see dashboard </h1>
  </div>
</template>

<script>
import PieChart from "./charts/PieChart.js";
import BarChart from "./charts/BarChart.js";
import SimpleMap from "./SimpleMap.vue"
import json from '@/json/macDB.json';

export default {
  components: {
    PieChart,
    BarChart,
    SimpleMap
  },
  macDB: json,
  data() {
    return {
      isMounted: false,
      devices: [],
      types: [],
      tvs: [],
      vendors: [],
      vendorCountries: [],
      vendorData: {
        hoverBackgroundColor: "red",
        hoverBorderWidth: 10,
        labels: ["US", "CN", "KR", "FR"],
        datasets: [
          {
            label: "Devices",
            backgroundColor: ["#F06292", "#9575CD", "#7986CB", "#4DB6AC"],
            data: [0,0,0,0]
          }
        ]
      },
      vendorCountriesData: {},
      typeData: {
        hoverBackgroundColor: "red",
        hoverBorderWidth: 10,
        labels: ["WIFI", "BT", "BLE", "Other"],
        datasets: [
          {
            label: "Devices",
            backgroundColor: ["#F06292", "#9575CD", "#7986CB", "#4DB6AC"],
            data: [0,0,0,0]
          }
        ]
      }
    }
  },
  async mounted() {
    await this.getDevicesFromIDB();
    this.typeData.datasets[0].data = this.types;
    //await this.getVendorData();
    //this.vendorData.datasets[0].data = this.vendors;
    //this.vendorCountriesData.datasets[0].data = this.vendors;
    this.isMounted = true;
  },
  methods: {
    async getDevicesFromIDB() {
      this.devices = await this.$idb.SimpleIDB.get('devices');
      this.types = await this.$idb.SimpleIDB.get('types');
      this.tvs = await this.$idb.SimpleIDB.get('tvs');
    },
    devicesExist() {
      return this.devices != [];
    },
    getVendorData() {
      for(var x = 0; x < this.devices.length; x++) {
        var mac = this.devices[x].mac;
        var longestLength = 0;
        var longestIndex = -1;

        for(var i = 0; i < json.length; i++) {
          if(json[i].oui == mac.substring(json[i].oui.length, 0) && json[i].oui.length > longestLength) {
            longestIndex = i;
            longestLength = json[i].oui.length;
          }
        }
        
        var vendor = 'Unknown';
        var countryCode = '';

        if(longestIndex != -1) {
          vendor = json[longestIndex].companyName;
          countryCode = json[longestIndex].countryCode.toUpperCase().replace(/./g, char => String.fromCodePoint(char.charCodeAt(0)+127397) );
        }

        this.vendors[x] = vendor;
        this.vendorCountries[x] = countryCode;
      }
      
    }
  }
}
</script>

