<template>
  <div v-if="!uploaded" class="container">
    <div class="title">
      <h1> Upload csv file of scanned devices: </h1>
    </div>
    <upload-module v-if="!uploaded" v-on:upload="handleUpload"></upload-module>
  </div>
  <div v-else>
    <div class="uploaded">
      <h1 class="green-text"> Uploaded! </h1>
    </div>
  </div>
</template>

<script>
import UploadModule from './UploadModule';

export default {
  components: {
    UploadModule
  },
  data() {
    return {
      file: "",
      csv: "",
      devices: [],
      types: [],
      tvs: [],
      uploaded: false,

      initialized: false
    }
  },
  mounted() {

  },
  methods: {
    handleUpload(uploadedDevices) {
      console.log(uploadedDevices.length);
      this.devices = uploadedDevices;
      this.saveDevices();
    },
    saveDevices() {
      // Initialize IndexedDB
      this.initialize();

      // devices
      this.insertObject('devices', this.devices);

      // types
      this.insertObject('types', this.getTypes());

      // tvs
      this.insertObject('tvs', this.getTVs());

      // done
      this.uploaded = true;
    },
    getTypes() {
      var count = [0,0,0,0];
      var i;
      for(i = 0; i < this.devices.length; i++) {
        switch(this.devices[i].type) {
          case 'WIFI':
            count[0]++;
            break;
          case 'BT':
            count[1]++;
            break;
          case 'BLE':
            count[2]++;
            break;
          default:
            count[3]++;
        }
      }
      return count;
    },
    getTVs() {
      var tmp = [];
      var i;
      for(i = 0; i < this.devices.length; i++) {
        if(this.devices[i] && this.devices[i].ssid && this.devices[i].ssid.includes('TV')) {
          tmp.push(this.devices[i]);
        }
      }
      return tmp;
    },
    initialize () {
      this.initialized = true;
      this.$idb.SimpleIDB.initialize();
    },
    insertObject(key, val) {
      try {
        this.$idb.SimpleIDB.set(key, val)
      } catch(e) {
        console.log(e);
      }
    },
    removeObject (key) {
      try {
        this.$idb.SimpleIDB.remove(key)
      } catch(e) {
        console.log(e)
      }
    }
  }
};
</script>

