<template>
   <div id="mapContainer"></div>
</template>

<script>
import "leaflet/dist/leaflet.css";
import L from "leaflet";
import json from '@/json/macDB.json';

export default {
  name: 'SimpleMap',
  props: ['inSsid', 'inMac', 'inSsidCS', 'type'],
  macDB: json,
  data() {
    return {
      zoom: 14,
      center: [0, 0],
      url: 'https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png',
      myRenderer: L.canvas({padding: 0.5}),
      map: '',
      devices: [],
      ouis: []
    }
  },
  async mounted() {
    await this.getDevicesFromIDB();

    if(this.devices.length > 0) {
      this.center = [parseFloat(this.devices[0].currentLatitude), parseFloat(this.devices[0].currentLongitude)]
    } else {
      this.zoom = 1;
    }

    this.setupLeafletMap();

    // Get vendor
    json.forEach(x => { this.ouis.push(x.oui); });
  },
  methods: {
    async getDevicesFromIDB() {
      let result = await this.$idb.SimpleIDB.get('devices');
      this.devices = result;
    },
    setupLeafletMap: function () {
      const mapDiv = L.map("mapContainer").setView(this.center, this.zoom);

      

      L.tileLayer(
        "https://api.mapbox.com/styles/v1/{id}/tiles/{z}/{x}/{y}?access_token={accessToken}",
        {
          attribution:
            'Map data (c) <a href="https://www.openstreetmap.org/">OpenStreetMap</a> contributors, <a href="https://creativecommons.org/licenses/by-sa/2.0/">CC-BY-SA</a>, Imagery (c) <a href="https://www.mapbox.com/">Mapbox</a>',
          maxZoom: 25,
          id: "mapbox/streets-v11",
          accessToken:'pk.eyJ1Ijoibmljb2xhaXJvbm5pbmciLCJhIjoiY2tsOWh2eXM5MGJ1ZzJvczh0NWRtZDZ0YSJ9.rv1Dff1B42xEfbLPVbBnrQ',
        }
      ).addTo(mapDiv);

      var self = this;

      // When opening popup
      mapDiv.on('popupopen', function(e) {
          // Get specific marker
          var marker = e.popup._source;

          var mac = marker.options.mac;
          var longestLength = 0;
          var longestIndex = -1;

          for(var i = 0; i < self.ouis.length; i++) {
            if(self.ouis[i] == mac.substring(self.ouis[i].length, 0) && self.ouis[i].length > longestLength) {
              longestIndex = i;
              longestLength = self.ouis[i].length;
            }
          }
          
          var vendor = 'Unknown';
          var countryCode = '';

          if(longestIndex != -1) {
            vendor = json[longestIndex].companyName;
            countryCode = json[longestIndex].countryCode.toUpperCase().replace(/./g, char => String.fromCodePoint(char.charCodeAt(0)+127397) );
          }

          // Dynamically set content of popup
          marker._popup.setContent('[' + 
            marker.options.type + ']<br>' + 
            'SSID: ' + marker.options.ssid + '<br>' +
            'MAC: ' + marker.options.mac + '<br>' + 
            'Authorization Mode: ' + marker.options.authMode + '<br>' +
            'First Seen: ' + marker.options.firstSeen + '<br>' +
            'Channel: ' + marker.options.channel + '<br>' +
            'RSSI: ' + marker.options.rssi + '<br>' +
            'Vendor: ' + vendor + ' ' + countryCode +
            '<br><button id="button-submit"> Filter this device </button>');

          const buttonSubmit = L.DomUtil.get('button-submit');
          L.DomEvent.addListener(buttonSubmit, 'click', function() {
            self.$emit('filter', marker.options.mac);
          });
      });


      for (var i = 0; i < this.devices.length - 1; i += 1) {
        var tmp = this.devices[i];

        if( (!this.inSsid || tmp.ssid.toUpperCase().includes(this.inSsid.toUpperCase())) && 
            (!this.inSsidCS || tmp.ssid.includes(this.inSsidCS)) &&
            (!this.inMac || tmp.mac.includes(this.inMac)) &&
            (!this.type || tmp.type.includes(this.type))
            ) {

          L.circleMarker([parseFloat(tmp.currentLatitude),parseFloat(tmp.currentLongitude)], {
            renderer: this.myRenderer,
            radius: 5,
            color: this.getColor(tmp.type),
            fill: this.getColor(tmp.type),
            type: tmp.type,
            ssid: tmp.ssid,
            mac: tmp.mac,
            authMode: tmp.authMode,
            firstSeen: tmp.firstSeen,
            channel: tmp.channel,
            rssi: tmp.rssi,

          }).addTo(mapDiv).bindPopup();
        }
      }
    },
    getColor: function (type) {
      switch (type) {
        case 'WIFI':
          return  '#2E7D32';
        case 'GSM':
        case 'LTE':
          return '#C62828';
        case 'BT':
          return '#1565C0';
        case 'BLE':
          return '#283593';
        default:
          return 'white';
      }
    },
    thisDevice(mac) {
      console.log(mac);
      this.inMac = mac;
    },
    createButton(label, container) {
      var btn = L.DomUtil.create('button', '', container);
      btn.setAttribute('type', 'button');
      btn.innerHTML = label;
      return btn;
    },
    sharedStart(array) {
      var A = array.concat().sort(), 
      a1 = A[0], a2= A[A.length-1], L= a1.length, i= 0;
      while(i < L && a1.charAt(i) === a2.charAt(i)) i++;
      return a1.substring(0, i);
    }
  }
}
</script>

<style scoped>
#mapContainer {
    height: 100%;
    width: 100%;
}
</style>